<template>
  <div class="partner-page">
    <div class="page-header">
      <div class="width-center">
        Invest in
      </div>
      <input v-model="channel" type="text" class="header-type" id="header-type-name" placeholder="you" spellcheck="false"/>
    </div>
    <div class="sign-up-pane">
      <div class="partner-signup-description">
        Grow your channel, collaborate with others, build your community. All 100% free.
      </div>
      <router-link to="/partner-signup" class="partner-signup"> Sign up now{{channel.length ? ', '+channel : ''}}</router-link>
    </div>
    <div class="raise-funds-pane pane">
      <div class="pane-left row-icon hidden-low" v-infocus="'showElement'">
        <i class="fas fa-hiking icon-person" id="icon-hiker"></i>
        <i class="fas fa-mountain icon-mountain"></i>
      </div>
      <div class="pane-right hidden-low" v-infocus="'showElement'">
        <div class="header">
          Grow your channel, no matter what size
        </div>
        <div class="description">
          Whether you're a small vlogger saving up for a new camera, or you're a youtube star saving up for an island to shoot on, bazzled has you covered
        </div>
      </div>
    </div>
    <div class="ccn-pane pane">
      <div class="pane-left hidden-low" v-infocus="'showElement'">
        <div class="header">
          Join the CCN
        </div>
        <div class="description">
          Find other youtuber's in your field to collaborate, exchange ideas, and grow using the Creator Colab Network (CCN)
        </div>
      </div>
      <div class="pane-right row-grid-names keep-width hidden-low" v-infocus="'showElement'">
        <transition-group name="name-group">
          <div v-for="name in randomNames" :key=name class="ran-name" :ref="'ran-name-'+name">
            {{name}}
          </div>
        </transition-group>
      </div>
    </div>
    <div class="grow-pane pane">
      <div class="pane-left keep-width hidden-low" v-infocus="'showElement'">
        <img src="@/assets/us-map.png" class="us-map" id="us-map"/>
        <div v-for="dot in redDots" class="gradient-map" :key="dot.id" :ref="'red-dot-'+dot.id" />
      </div>
      <div class="pane-right hidden-low" v-infocus="'showElement'">
        <div class="header">
          Build a community
        </div>
        <div class="description">
          Bazzled helps you strengthen your channel's viewer base by giving people a reason to care about your success. 
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      channel: '',
      randomNames: ['Alisha Finn','Stacie Merrill','Candice Salinas','Ana Roach','Marlene Chavez','Cesar Sanderson','Denni Hooper','Penny Ryan','Johnnie Weaver','Damon Heath','Audrey McGowan','Jared Williams','Devin Walter','Faith Hyde','June Everett','Clifford Meyers','Leroy Oneill','Lana Romero','Jack Groves','Maggie Chaney'],
      shuffleNames: true,
      firstShuffle: true,
      redDots: Array.apply(null, { length: 10 })
        .map(function (_, index) { 
          return {
            id: index,
            number: index % 9 + 1
          }
        }),
      killAllAsync: false,
    }
  },
  mounted(){
    const width  = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
    if(width > 600)
      document.getElementById('header-type-name').focus();
    /*else
      document.getElementById('header-type-name').style = 'border: 1px solid gray; padding-left: 2px;';*/
    this.shuffle();
    this.scatterRedDots();
  },
  methods: {
    sleep: function(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    },
    shuffle: async function(){
      const len = this.randomNames.length;
      let indexes = [];
      for(let i = 0; i < len; i++){
        if(indexes.includes(i))
          continue;
        let ranI = Math.floor(Math.random() * (len));
        let attempts = 0;
        while(indexes.includes(ranI) || ranI == i){
          ranI = Math.floor(Math.random() * len);
          attempts++;
          if(attempts > 10)
            break;
        }
        [this.randomNames[i], this.randomNames[ranI]] = [this.randomNames[ranI], this.randomNames[i]];
        indexes.push(i);
        indexes.push(ranI);
        //await this.sleep(50);
      }
      await this.sleep(700);
      if(this.killAllAsync) return;
      if(this.shuffleNames)
        this.shuffle();
    },
    stopShuffle: async function(){
      await this.sleep(2000);
      if(this.firstShuffle && window.scrollY >= 800 && this.shuffleNames){
        this.shuffleNames = false;
        this.firstShuffle = false;
        this.colorNames();
      }
    },
    colorNames: async function(){
      await this.sleep(1000);
      if(this.killAllAsync) return;
      //blue red yellow green (google color scheme) + random light-gray
      const colors = ['#4285F4', '#EA4335', '#FBBC05', '#34A853', '#D0D0D0'];
      let indexes = [];
      for(let color of colors){
        for(let i = 0; i < 4; i++){
          let ranI = Math.floor(Math.random() * this.randomNames.length)
          while(indexes.includes(ranI)){
            ranI = Math.floor(Math.random() * this.randomNames.length)
          }
          indexes.push(ranI);
          this.$refs['ran-name-'+this.randomNames[ranI]].style.background = color;
          this.$refs['ran-name-'+this.randomNames[ranI]].title = color;
          await this.sleep(100);
          if(this.killAllAsync) return;
        }
        await this.sleep(200);
        if(this.killAllAsync) return;
      }
      this.sortNames();
    },
    sortNames: async function(){
      const colors = ['#4285F4', '#EA4335', '#FBBC05', '#34A853', '#9F19D7'];
      let i = 0;
      for(const color of colors){
        for(let n = 0; n < 4; n++){
          for(let iNew = i; iNew < this.randomNames.length; iNew++){
            if(color == this.$refs['ran-name-'+this.randomNames[iNew]].title){
              [this.randomNames[i], this.randomNames[iNew]] = [this.randomNames[iNew], this.randomNames[i]];
              i++;
              if(i != iNew)
                await this.sleep(400);
                if(this.killAllAsync) return;
              break;
            }
          }
        }
      }
      //clear colors and redo shuffle

      await this.sleep(1000);
      if(this.killAllAsync) return;
      //pull blocks on same row together
      for(let i = 0; i < this.randomNames.length; i++){
        this.$refs['ran-name-'+this.randomNames[i]].style.margin = '5px 0';
        if(i % 5 == 0)
          await this.sleep(50);
          if(this.killAllAsync) return;
      }
      await this.sleep(1500);
      if(this.killAllAsync) return;
      //remove background and separate blocks
      for(let i = 0; i < this.randomNames.length; i++){
        this.$refs['ran-name-'+this.randomNames[i]].style.background = 'none';
        this.$refs['ran-name-'+this.randomNames[i]].style.margin = '5px';
        await this.sleep(20);
        if(this.killAllAsync) return;
      }
      await this.sleep(500);
      if(this.killAllAsync) return;
      this.shuffleNames = true;
      this.shuffle();
      setTimeout(() => {
        this.shuffleNames = false;
        this.colorNames();
      }, 2000);
    },
    scatterRedDots: async function(){
      var lefts =   [20,  0, 30,  270, 270,  340, 490, 480,  460, 470];
      var bottoms = [250, 0, -80, 40,  -160, 90, 200, 150, 70, -260];
      
      if(document.getElementById('us-map').width < 500){
        lefts =   lefts.map(x => x*3/5.6 - 10);
        bottoms = bottoms.map(x => x*3/5.6);
      }
  
      for(let i = 0; i < this.redDots.length; i++){
      this.$refs['red-dot-'+i].style.width = '20px';
      this.$refs['red-dot-'+i].style.height = '20px';
      }

      for(let i = 0; i < this.redDots.length; i++){
        this.$refs['red-dot-'+i].style.margin = '0 0 '+(bottoms[i]+15)+'px '+(lefts[i]+15)+'px';
        this.$refs['red-dot-'+i].style.opacity = 1;
        await this.sleep(100);
        if(this.killAllAsync) return;
      }

      //deals with glitch on ios safari in which margin-bottom is ignored. Need to test desktop safari
      var rect1 = this.$refs['red-dot-0'].getBoundingClientRect();
      var rect2 = this.$refs['red-dot-1'].getBoundingClientRect();
      var overlaps = rect1.top <= rect2.bottom && rect2.top <= rect1.bottom;
      if(overlaps){
        for(let i = 0; i < this.redDots.length; i++){
          //deals with glitch on ios safari in which margin-bottom is ignored. Need to test desktop safari
          this.$refs['red-dot-'+i].style.marginTop = (0 - bottoms[i] * .5) + 'px';
          await this.sleep(100);
          if(this.killAllAsync) return;
        }
      }
      await this.sleep(2000);
      if(this.killAllAsync) return;

      for(let i = 0; i < this.redDots.length; i++){
        this.$refs['red-dot-'+i].style.margin = '0 0 '+(bottoms[i])+'px '+(lefts[i])+'px';
        if(overlaps)
          this.$refs['red-dot-'+i].style.marginTop = (0 - bottoms[i] * .5) + 'px';
        this.$refs['red-dot-'+i].style.width = '50px';
        this.$refs['red-dot-'+i].style.height = '50px';
        await this.sleep(100);
        if(this.killAllAsync) return;
      }
      await this.sleep(2000);
      if(this.killAllAsync) return;

      for(let i = 0; i < this.redDots.length; i++){
        this.$refs['red-dot-'+i].style.opacity = 0;
        await this.sleep(100);
        if(this.killAllAsync) return;
      }
      await this.sleep(2000);
      if(this.killAllAsync) return;
      this.scatterRedDots();
    }
    
  },
  created: function() {
    window.addEventListener('scroll',this.stopShuffle);
  },
  beforeUnmount: function(){
    this.killAllAsync = true;
  },
  unmounted: function() {
    window.removeEventListener('scroll', this.stopShuffle); 
  },
  directives: {
    infocus: {
      isLiteral: true,
      mounted: (el, binding) => {
        let f = () => {
          let rect = el.getBoundingClientRect()
          let inView = (
            rect.width > 0 &&
            rect.height > 0 &&
            rect.top >= 0 &&
            (rect.top + 100) <= (window.innerHeight || document.documentElement.clientHeight)
          )
          if (inView) {
            el.classList.add(binding.value)
            window.removeEventListener('scroll', f)
          }
        }
        window.addEventListener('scroll', f)
        f()
      }
    }
  }
}
</script>
<style scoped>
  .partner-page{
    padding: 180px 0;
  }
  .page-header{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    font-size: 64px;
    font-weight: 600;
    margin-bottom: 20px;
    margin-left: 2em;
  }
  .width-center{
    width: 50%;
    text-align: right;
  }
  .header-type{
    margin-left: 20px;
    color: var(--primary-color);
    border: none;
    width: 50%;
    font-size: inherit;
    font-weight: 600;
    overflow: hidden;
    padding: 0;
  }
  .header-type:focus{
    outline: none;
  }
  .sign-up-pane{
    padding: 0 60px;
    margin: 300px 0 120px;
  }
  .partner-signup-description{
    color: var(--light-gray);
    font-size: 24px;
    margin-bottom: 30px;
  }
  .partner-signup{
    text-decoration: none;
    color: white;
    padding: 10px 35px;
    background: var(--primary-color);
    border-radius: 30px;
    transition: .3s all;
    font-size: 20px;
    white-space: nowrap;
  }
  .partner-signup:hover{
    background: var(--primary-color-light);
  }
  .pane{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 150px 30px;
  }
  .pane-left,.pane-right{
    display: flex;
    flex-direction: column;
    width: min(50%, 560px);
    justify-content: center;
  }
  .pane-left{
    margin-right: 100px;
  }
  .description{
    color: var(--dark-gray);
    font-size: 20px;
    line-height: 140%
  }
  .row-icon{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
  }
  .icon-person{
    font-size: 40px;
    color: var(--primary-color);
    margin-right: 100px;
    transition: .5s all;
  }
  .icon-mountain{
    font-size: 180px;
    color: var(--light-gray);
  }
  .row-grid-names{
    flex-direction: row !important;
    align-items: center;
    flex-wrap: wrap;
    max-width: 680px;
  }
  .name-group-move{
    transition: transform 1s ease;
  }
  .ran-name{
    padding: 9px;
    margin: 5px;
    width: 110px;
    height: 40px;
    transition: background 0.3s, transform 1s, margin 0.3s;
  }
  .us-map{
    width: 100%;
  }
  .gradient-map{
    height: 20px;
    width: 20px;
    position: absolute;
    background: radial-gradient(circle, rgba(255,0,0,.8) 0%, rgba(255,0,0,.2) 60%, rgba(255,255,255,0) 100%);    
    border-radius: 50%;
    transition: opacity 1s, width .5s, height .5s, margin .5s;
    opacity: 0;
    display: inline !important;
  }

  .keep-width{
    width: 560px !important;
  }
  @media screen and (max-width: 1296px) {
    .pane-left{
      margin-right: 0;
      margin-bottom: 60px;
    }
  }
  @media screen and (max-width: 800px) {
    .page-header{
      font-size: 50px;
      font-size: 8vw;
    }
    .header-type{
      margin-left: calc(1em / 3);
    }
    .sign-up-pane{
      margin-top: 150px;
    }

    .pane{
      flex-direction: column-reverse;
      align-items: center;
    }
    .ccn-pane{
      flex-direction: column;
    }
    .pane-left,.pane-right{
      width: 100%;
    }
    .row-icon{
      justify-content: center;
      margin-top: 30px;
    }
  }
  @media screen and (max-width: 620px) {
    .keep-width{
      min-width: 300px !important;
      width: 300px !important;
    }
    .pane{
      padding: 50px 30px;
    }
    .ran-name{
      padding: 5px;
    }
    .partner-page{
      padding-bottom: 100px;
    }
  }
  @media screen and (max-width: 400px) {
    .icon-mountain{
      font-size: 100px;
    }
  }
  .hidden-low {
    opacity: 0;
    transform: translate(0, 100px);
  }
  .showElement {
    opacity: 1;
    transform: translate(0, 0);
    -webkit-transition: all 0.5s ease-out;
    -moz-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
  }
</style>